import clsx from "clsx";
import style from "./Modal.module.scss";

const Modal = ({
  className,
  contentClassName,
  title,
  handleClose,
  isOpen,
  children,
}) => {
  return (
    isOpen && (
      <div
        className={clsx(style.backdrop, className)}
        style={{ overflow: "scroll" }}
      >
        <div className={clsx(style.content, contentClassName)}>{children}</div>
      </div>
    )
  );
};

export default Modal;
